@import "../default.scss";

.body-contacts {
  margin-top: 100px;
  padding-top: 102px;
  padding-bottom: 90px;
  background-color: #FBFBFB;
  .body-contacts__container {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    @media (max-width: 960px) {
      flex-direction: column;
    }
    .contacts-info-block {
      margin-top: 11px;
      &__title {
        margin-left: -2px;
        font-family: "Raleway";
        font-style: normal;
        font-weight: 800;
        font-size: 45px;
        line-height: 53px;
        letter-spacing: 0.01em;
        color: $cyanBlue;
        @media (max-width: $tablet) {
          grid-column-start: 1;
          grid-column-end: 3;
          text-align: start;
        }
        @media (max-width: 660px) {
          grid-column-start: 1;
          grid-column-end: 1;
        }
      }
      .social-buttons {
        margin-left: 32px;
        @media (max-width: 660px) {
          justify-self: flex-end;
          margin-left: auto;
        }
      }
      .contacts-telephone-block {
        margin-top: 71px;
        display: flex;
        align-items: center;
        gap: 14px;
        .icon-phone {
          color: $darkGreen;
        }
        &__phone-numbers {
          display: flex;
          flex-direction: column;
          gap: 4px;
        }
        &__phone-number {
          display: block;
          font-family: "Montserrat";
          font-style: normal;
          font-weight: 500;
          font-size: 14px;
          line-height: 21px;
          letter-spacing: 0.01em;
          color: $cyanBlue;
        }
        @media (max-width: $tablet) {
          margin-top: 0;
        }
        @media (max-width: 660px) {
          width: 100%;
        }
      }
      .contacts-info-block__mail-info {
        margin-top: 42px;
        position: relative;
        font-family: "Montserrat";
        display: block;
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 21px;
        letter-spacing: 0.01em;
        margin-left: 30px;
        &::before {
          color: $darkGreen;
          position: absolute;
          left: -30px;
          font-family: "icomoon";
          content: "\e917";
          font-size: 13px;
        }
        @media (max-width: $tablet) {
          margin-top: 0;
        }
      }
      .address-info {
        margin-top: 46px;
        padding-left: 30px;
        &__title {
          position: relative;
          font-family: "Raleway";
          font-style: normal;
          font-weight: 800;
          font-size: 17px;
          line-height: 20px;
          letter-spacing: 0.01em;
          color: $cyanBlue;
          &::before {
            color: $darkGreen;
            position: absolute;
            left: -30px;
            font-family: "icomoon";
            content: "\e916";
            font-size: 16px;
          }
        }
        &__address {
          margin-top: 17px;
          font-family: "Montserrat";
          font-style: normal;
          font-weight: 500;
          font-size: 14px;
          line-height: 21px;
          letter-spacing: 0.01em;
          color: $cyanBlue;
        }
        @media (max-width: $tablet) {
          margin-top: 0;
        }
      }
      .address-factory-info {
        margin-top: 37px;
        padding-left: 30px;
        &__title {
          position: relative;
          font-family: "Raleway";
          font-style: normal;
          font-weight: 800;
          font-size: 17px;
          line-height: 20px;
          letter-spacing: 0.01em;
          color: $cyanBlue;
          &::before {
            position: absolute;
            left: -30px;
            font-family: "icomoon";
            content: "\e915";
            color: $darkGreen;
            font-size: 15px;
          }
        }
        &__address {
          margin-top: 17px;
          font-family: "Montserrat";
          font-style: normal;
          font-weight: 500;
          font-size: 14px;
          line-height: 21px;
          letter-spacing: 0.01em;
          color: $cyanBlue;
          @media (max-width: $tablet) {
            margin-top: 0;
          }
        }
        .icon-tree {
        }
        @media (max-width: $tablet) {
          margin-top: 0;
        }
      }
      @media (max-width: 960px) {
        display: grid;
        width: 100%;
        grid-template-columns: max-content max-content;
        justify-content: space-between;
        row-gap: 30px;
      }
      @media (max-width: 660px) {
        grid-template-columns: 1fr;
      }
    }
    .contacts-info-block__map {
      flex: 0 0 57.2%;
      height: 518px;
      @media (max-width: 960px) {
        flex: auto;
        margin-top: 30px;
        width: 100%;
        height: 400px;
      }
    }
  }
}
