$darkGreenHover: #00572e;
$darkGreen: #337d5a;
$green: #93b474;
$cyanBlue: #1a2f3f;
$gray: #8a8a8a;
$white: #ffffff;
$darkBlue: #1a2f3f;

$tablet: 960px;
$mobile: 600px;

@mixin mont-72 {
  font-family: "Montserrat";
  font-weight: 800;
  font-size: 72px;
  line-height: 52px;
}

@mixin mont-15 {
  font-family: "Montserrat";
  font-weight: 500;
  font-size: 15px;
  line-height: 52px;
}

@mixin mont-14 {
  font-family: "Montserrat";
  font-weight: 500;
  font-size: 14px;
  line-height: 52px;
}

@mixin rale-62 {
  font-family: "Raleway";
  font-weight: 800;
  font-size: 62px;
  line-height: 52px;
}

@mixin rale-45 {
  font-family: "Raleway";
  font-weight: 800;
  font-size: 45px;
  line-height: 52px;
}

@mixin rale-25 {
  font-family: "Raleway";
  font-weight: 500;
  font-size: 25px;
  line-height: 52px;
}

@mixin rale-18 {
  font-family: "Raleway";
  font-weight: 500;
  font-size: 18px;
  line-height: 52px;
}

@mixin rale-16 {
  font-family: "Raleway";
  font-weight: 500;
  font-size: 16px;
  line-height: 52px;
}

.custom-select {
  position: relative;
  font-family: "Montserrat";
  select {
    display: none;
  }

  .select-selected {
    background-color: $white;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 3px;
    &:hover {
      background: rgba(51, 125, 90, 0.1);
    }
    &:after {
      font-family: icomoon;
      color: $green;
      font-size: 6px;
      content: "\e922";
      transform: rotate(180deg);
    }
    &.select-arrow-active {
      border: 1.5px solid $darkGreen;
    }
    &.select-arrow-active:after {
      border-color: transparent transparent $green transparent;
      transform: rotate(0deg);
    }
  }

  .select-items div,
  .select-selected {
    color: $darkBlue;
    padding: 3px 0px 3px 5px;
    border: 1px solid transparent;
    border-color: transparent transparent rgba(0, 0, 0, 0.1) transparent;
    cursor: pointer;
    user-select: none;
    border: 0.5px solid rgba(0, 0, 0, 0.15);
  }

  .select-items {
    position: absolute;
    background-color: $white;
    top: 100%;
    left: 0;
    right: 0;
    z-index: 99;
  }

  .select-hide {
    display: none;
  }

  .select-items .same-as-selected {
    background-color: $darkGreen;
    color: $white;
  }

  .select-items div:hover,
  .same-as-selected {
    background-color: $darkGreen;
    color: $white;
  }
}
